/** NORMALIZE
------------------------------------------------------------*/
@import 'vendor/font-awesome.min.css';
@import 'vendor/swiper.min.css';
@import 'ie_fix.css';
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: 700;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background: #ff0;
  color: #000000;
}
small {
  font-size: 80%;
}
sub {
  font-size: 50%;
  line-height: 1;
  vertical-align: text-bottom;
}
sup {
  font-size: 50%;
  line-height: 1;
  vertical-align: text-top;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace,monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type=checkbox],
input[type=radio] {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}
input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: 700;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
@font-face {
  font-family: 'fontawesome-webfont';
  src: url('../fonts/fontawesome-webfont/fontawesome-webfont.woff'), url('../fonts/fontawesome-webfont/fontawesome-webfont.woff2');
}
@font-face {
  font-family: 'MyriadProItalic';
  src: url('../fonts/MyriadProItalic/MyriadProItalic.woff'), url('../fonts/MyriadProItalic/MyriadProItalic.woff2');
}
* {
  box-sizing: border-box;
}
body {
  font-family: 'Open Sans';
  line-height: 1.2;
  overflow-x: hidden;
}
body.search_box-visible {
  height: 100vh;
  overflow: hidden;
}
body.search_box-visible .search_box {
  display: block;
}
b,
strong {
  font-weight: bold;
}
i,
em {
  font-style: italic;
}
.text_logo:before {
  content: '';
  position: absolute;
  z-index: 9;
  top: 0px;
  right: 0px;
  width: 833px;
  height: 722px;
  mix-blend-mode: screen;
  -webkit-transform: translateX(50%) translateY(-40%);
  -moz-transform: translateX(50%) translateY(-40%);
  transform: translateX(50%) translateY(-40%);
  background: url('../images/sunshine.png');
}
.wrapper {
  width: 100%;
  max-width: 975px;
  margin: 0 auto;
  padding: 0 15px;
}
button,
.button {
  font-family: 'Playfair Display';
  font-style: italic;
  font-size: 20px;
  padding-top: 0.45em;
  padding-bottom: 0.45em;
  padding-left: 1.75em;
  padding-right: 1.75em;
  background-color: #ffffff;
  margin-top: 0.2em;
  margin-bottom: 0.2em;
  text-decoration: none;
  color: #000000;
  display: inline-block;
  border: none;
  cursor: pointer;
  text-align: center;
  box-shadow: 0px 0 1px rgba(0, 0, 0, 0.2);
}
button:hover,
.button:hover,
button:active,
.button:active {
  background-color: #fdd9cc;
  box-shadow: 8px 0 24px rgba(0, 0, 0, 0.25);
}
button.button-shadow,
.button.button-shadow {
  box-shadow: 4px 0 12px rgba(0, 0, 0, 0.15);
}
button.button-shadow:hover,
.button.button-shadow:hover,
button.button-shadow:active,
.button.button-shadow:active {
  box-shadow: 8px 0 24px rgba(0, 0, 0, 0.25);
}
button.button-inverse,
.button.button-inverse {
  background-color: #fdd9cc;
  border: 1px solid;
  border-color: transparent;
}
button.button-inverse:hover,
.button.button-inverse:hover,
button.button-inverse:active,
.button.button-inverse:active {
  background-color: #ffffff;
  border-color: #9e9e9e;
}
main input[type='checkbox'] {
  display: none;
}
main input[type='checkbox'] + label {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 1px solid black;
  background-color: #f3f4f6;
  flex-shrink: 0;
  margin-bottom: 0;
}
main input[type='checkbox']:checked + label {
  background-image: url(data:image/jpeg;base64,);
  background-position: -1px -1px;
}
nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
nav ul.nav_menu li:hover .sub-menu {
  display: block;
}
nav ul.sub-menu {
  display: none;
  position: absolute;
  padding: 0;
}
.bold_text {
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.simple_text {
  font-family: 'Playfair Display';
}
.italic_text {
  font: 18px 'Playfair Display';
  font-style: italic;
}
.online_link {
  font-size: 14px;
  font-weight: bold;
  color: #000000;
  text-decoration: underline;
  text-transform: uppercase;
  letter-spacing: 3px;
  display: inline-block;
}
.pink_rect {
  font-size: 15px;
  text-transform: uppercase;
  outline: none;
  background-color: #fdd9cc;
  border: none;
  padding: 15px 35px;
  letter-spacing: 2px;
  min-width: 170px;
  text-decoration: none;
}
.social {
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.social a {
  text-decoration: none;
  color: #ffffff;
  display: inline-block;
  background-color: #000000;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;
  margin: 5px;
  font-size: 14px;
}
.social.inverted a {
  background-color: #ffffff;
  color: #000000;
}
.block_title {
  font: 44px 'Playfair Display';
  font-style: italic;
  text-align: center;
  position: relative;
}
.block_title.striped {
  margin-top: 20px;
}
.block_title.striped:before {
  content: '';
  display: block;
  width: 40px;
  height: 3px;
  background-color: #000000;
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
}
.block_title.striped.white:before {
  background-color: #ffffff;
}
.service_name {
  text-align: center;
  font-size: 15px;
  font-weight: bold;
  letter-spacing: 2px;
  text-decoration: underline;
  text-transform: uppercase;
  line-height: 1.8;
  padding: 12px 0 10px;
  cursor: pointer;
}
.price_list .price_head,
.price_list .price_row,
.price_list .price_cellar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.price_list .price_head,
.price_list .price_cellar {
  border: 1px solid #000000;
  border-left: none;
  border-right: none;
  font-size: 15px;
  font-weight: bold;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 25px 0;
}
.price_list .price_cellar {
  border-bottom: none;
  margin-top: 20px;
}
.price_list .price_row {
  padding: 20px 0;
  position: relative;
  z-index: 1;
  flex-wrap: wrap;
}
.price_list .price_row:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: #f3f4f6;
  z-index: -1;
}
.price_list .price_row:hover:after {
  width: 100%;
  transition-property: width;
  transition-duration: 0.5s;
}
.price_list .price_row .s_name {
  flex-grow: 1;
  text-align: left;
  text-decoration: none;
  color: #000000;
}
.price_list .price_row .s_desc {
  font-style: italic;
  font-size: 14px;
  padding-left: 38px;
  padding-right: 76px;
  width: 100%;
}
.price_list input + label {
  margin-right: 25px;
}
.price_list .divider {
  width: 100%;
  height: 1px;
  background-color: #000000;
}
.price_list.with_bullits .s_name:before {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  margin-right: 25px;
  background-color: #fdd9cc;
}
.service_desc {
  color: #000000;
  line-height: 1.7;
  padding: 5px 0;
}
.service_desc.hidden {
  display: none;
}
.service_desc ul {
  padding: 15px 0;
  list-style: none;
}
.service_desc ul li {
  margin-bottom: 30px;
  position: relative;
  padding-left: 30px;
}
.service_desc ul li:before {
  content: '';
  position: absolute;
  top: 0.7em;
  left: 0;
  display: inline-block;
  width: 7px;
  height: 7px;
  background-color: #fdd9cc;
}
.news-post-item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 30px;
  text-decoration: none;
}
.news-post-item* {
  text-decoration: none;
}
.news-post-item .post-pic {
  width: 164px;
  height: 164px;
  position: relative;
  flex-shrink: 0;
  margin-right: 30px;
}
.news-post-item .post-pic img {
  margin-right: 30px;
  max-width: 164px;
  height: auto;
}
.news-post-item .post-pic .image_fit_replacer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.news-post-item .post-title {
  margin-bottom: 0;
  line-height: 1.2;
}
.news-post-item .post-date {
  line-height: 1;
}
.news-post-item .post-text p {
  margin-top: 0;
}
@media (max-width: 480px) {
  .news-post-item .post-pic {
    display: none;
  }
}
.sln-service-list {
  /*justify-content: space-between*/
}
.sln-service-list .service_img {
  text-align: center;
}
.sln-service-list .service_img + h2 {
  width: 100%;
}
@media (max-width: 1366px) {
  .sln-service-list .service_img img {
    width: 185px;
    height: 185px;
  }
  .sln-service-list .service_img + h2 {
    font-size: 0.6em !important;
  }
}
@media (max-width: 992px) {
  .sln-service-list .service_img img {
    width: 164px;
    height: 164px;
  }
  .sln-service-list .service_img + h2 {
    font-size: 0.6em !important;
  }
}
.square_block .wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 110px 20px;
}
.square_block .square_block-wrapper {
  background-color: #fdd9cc;
  color: #000000;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  text-align: center;
  max-width: 100%;
}
.square_block .online_link {
  color: #000000;
  font-size: 16px;
}
.square_block .online_link:hover {
  color: #ffac8f;
}
.square_block .square_block-expand {
  width: 0;
  display: inline-block;
  padding-top: 100%;
  float: left;
}
.square_block .block_title {
  font-size: 40px;
}
.square_block .discount {
  font-size: 120px;
}
.square_block .italic_text {
  font-size: 30px;
}
.square_block .square_block-content {
  padding: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.square_block .square_block-content > * {
  max-width: 100%;
}
@media (max-width: 480px) {
  .square_block .wrapper {
    padding: 20px 20px;
  }
  .square_block .online_link {
    font-size: 10px;
  }
  .square_block .block_title {
    font-size: 24px;
  }
  .square_block .bold_text {
    font-size: 14px;
  }
  .square_block .discount {
    font-size: 60px;
  }
  .square_block .italic_text {
    font-size: 20px;
  }
  .square_block .square_block-content {
    padding: 20px 10px;
  }
  .square_block .block_title.striped {
    margin-top: initial;
    margin-bottom: 20px;
  }
  .square_block .block_title.striped:before {
    top: initial;
    bottom: -15px;
  }
}
.flex-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.flex-row > * {
  margin-right: 20px;
  margin-bottom: 20px;
}
@media (max-width: 640px) {
  .flex-row {
    flex-direction: column;
  }
  .flex-row > * {
    margin-right: 0;
  }
}
header {
  background-color: #ffffff;
  box-shadow: -4px 7px 7px 0px rgba(0, 0, 0, 0.06);
  position: relative;
  height: 90px;
}
header .wrapper {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
  flex-shrink: 0;
  height: 100%;
  max-width: 1120px;
}
header .logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
header .logo img {
  width: 66px;
  height: 66px;
}
header nav {
  flex-grow: 1;
  display: flex;
  align-items: stretch;
  justify-content: center;
}
header nav ul.nav_menu {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-wrap: wrap;
}
header nav ul.nav_menu > li {
  padding: 6px 12px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  position: relative;
  white-space: nowrap;
}
header nav ul.nav_menu > li:hover > a,
header nav ul.nav_menu > li.current-menu-item > a {
  color: #ffbea7;
  /*text-shadow: 0px 0px 1px rgba(0, 0, 0, 0.5);*/
}
header nav ul.nav_menu > li > a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: #000000;
  font-size: 15px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}
header nav ul.sub-menu {
  top: 100%;
  left: -22px;
  box-shadow: 4px 7px 7px 0px rgba(0, 0, 0, 0.06);
  z-index: 5;
}
header nav ul.sub-menu > li {
  min-height: 0;
  line-height: 1;
  background-color: #ffffff;
  padding: 0;
  white-space: nowrap;
  justify-content: flex-start;
}
header nav ul.sub-menu > li:hover {
  background-color: #fdd9cc;
}
header nav ul.sub-menu > li > a {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 20px 30px;
  font-size: 15px;
  display: inline-block;
}
header nav a {
  position: relative;
}
header nav .menu-open-btn {
  display: none;
  width: 35px;
  height: 1em;
  margin-left: 10px;
  padding: 0 10px;
  font-size: 18px;
  color: #000000;
  cursor: pointer;
}
header nav .menu-open-btn:hover {
  color: #fdd9cc;
}
header nav .menu-item-icon {
  position: absolute;
  top: -18px;
  right: -18px;
  text-decoration: none;
  color: #ffffff;
  display: inline-block;
  background-color: #000000;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 50%;
  font-family: 'Playfair Display';
  font-weight: 900;
  font-size: 16px;
  box-shadow: 2px 0px 8px 0px rgba(0, 0, 0, 0.25);
}
header nav .menu-item-icon.actions-icon {
  padding-left: 2px;
}
header .search {
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .search > *[class*=fa] {
  display: inline-block;
  background-color: #fdd9cc;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;
  margin: 5px;
  margin-right: 28px;
  font-size: 14px;
  cursor: pointer;
}
header .search > *[class*=fa]:hover {
  background-color: #000000;
  color: #ffffff;
}
header .contacts {
  margin-left: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 14px;
  font-family: 'MyriadProItalic';
  align-self: center;
}
header .contacts a {
  color: #000000;
  text-decoration: none;
}
header .contacts .contacts-label {
  padding-right: 5px;
  font-weight: bold;
}
header .menu-toggle {
  padding: 10px 0;
  align-items: center;
  justify-content: center;
  display: none;
}
header .menu-toggle input {
  display: none;
}
header .menu-toggle input + label {
  display: inline-block;
  width: 60px;
  height: 60px;
  cursor: pointer;
  position: relative;
  margin-bottom: 0;
}
header .menu-toggle input + label span {
  display: block;
  position: absolute;
  height: 3px;
  width: 30px;
  background: #000000;
  opacity: 1;
  left: 15px;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  outline: 1px solid transparent;
  /*firefox anialiasing hack*/
}
header .menu-toggle input + label span:nth-child(1) {
  top: 20px;
}
header .menu-toggle input + label span:nth-child(2),
header .menu-toggle input + label span:nth-child(3) {
  top: 29px;
}
header .menu-toggle input + label span:nth-child(4) {
  top: 38px;
}
header .menu-toggle input:checked + label span:nth-child(1) {
  top: 29px;
  width: 0%;
  left: 50%;
}
header .menu-toggle input:checked + label span:nth-child(2) {
  transform: rotate(45deg);
}
header .menu-toggle input:checked + label span:nth-child(3) {
  transform: rotate(-45deg);
}
header .menu-toggle input:checked + label span:nth-child(4) {
  top: 29px;
  width: 0%;
  left: 50%;
}
header .mobile-menu {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-grow: 1;
}
/*@media (max-width: 992px) {*/
@media (max-width: 1132px) {
  header .menu-toggle {
    display: flex;
  }
  header .mobile-menu {
    display: none;
    position: absolute;
    z-index: 5;
    top: 90px;
    left: 0;
    width: 100%;
    flex-direction: column;
    background-color: #ffffff;
    padding-bottom: 10px;
    box-shadow: -4px 7px 7px 0px rgba(0, 0, 0, 0.06);
  }
  header .mobile-menu.visible {
    display: flex;
  }
  header nav {
    text-align: center;
  }
  header nav .menu-open-btn {
    display: inline-block;
  }
  header nav ul.nav_menu {
    flex-direction: column;
  }
  header nav ul.nav_menu li {
    display: block;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  header nav ul.nav_menu li:hover > a {
    color: #000000;
    border-color: #000000;
  }
  header nav ul.nav_menu li.menu-item-has-children {
    margin-left: 45px;
  }
  header nav ul.nav_menu li.menu-item-has-children > .sub-menu {
    margin-right: 45px;
  }
  header nav ul.nav_menu li a {
    display: inline-block;
    border-bottom: 1px solid;
    border-color: inherit;
  }
  header nav ul.nav_menu li a:hover {
    color: #fdd9cc;
    border-color: #fdd9cc;
  }
  header nav ul.sub-menu {
    display: block;
    position: static;
    box-shadow: none;
    overflow: hidden;
  }
  header nav ul.sub-menu > li {
    display: block;
    white-space: normal;
    line-height: 1.2;
  }
  header nav ul.sub-menu > li:hover {
    background-color: transparent;
