.musha-gallery {
  margin: 0 calc(-1*(100vw - 100%)/2);
  overflow: hidden;
}
.musha-gallery .musha-gallery-bg {
  background-color: #000000;
}
.musha-gallery .musha-gallery-bg > .wrapper {
  position: relative;
  z-index: 1;
}
.musha-gallery .musha-gallery-bg > .wrapper:before,
.musha-gallery .musha-gallery-bg > .wrapper:after {
  content: '';
  position: absolute;
  z-index: 2;
  top: 0;
  width: calc((100vw - 100%)/2);
  height: 100%;
  background-color: #000000;
  opacity: 0.65;
}
.musha-gallery .musha-gallery-bg > .wrapper:before {
  left: calc(-1*(100vw - 100% - 17px)/2);
}
.musha-gallery .musha-gallery-bg > .wrapper:after {
  right: calc(-1*(100vw - 100% - 17px)/2);
}
.musha-gallery .swiper-button {
  background-image: none;
  font-size: 60px;
  line-height: 0;
  color: #fdd9cc;
  text-align: center;
  padding: 0 10px;
  width: auto;
  height: auto;
  margin-top: auto;
  transform: translateY(-50%);
}
.musha-gallery .gallery-desc {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 30px 15px;
}
.musha-gallery .slide-desc {
  padding-right: 30px;
}
.musha-gallery .swiper-pagination {
  position: static;
  width: auto;
}
.musha-gallery .swiper-slide {
  position: relative;
}
.musha-gallery .swiper-slide:before {
  content: '';
  display: block;
  padding-top: 55%;
}
.musha-gallery .swiper-slide .image_fit_replacer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.musha-gallery .top-container {
  overflow: visible;
}
.musha-gallery .thumbs-container {
  background-color: #000000;
}
.musha-gallery .thumbs-container .swiper-slide {
  position: relative;
}
.musha-gallery .thumbs-container .swiper-slide.swiper-slide-active:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.25);
  border: 4px solid;
  border-color: #fdd9cc;
}
.shop-items {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  flex-wrap: wrap;
}
.shop-items .shop-item {
  margin-bottom: 20px;
  max-width: 295px;
  flex-grow: 1;
  width: 100%;
}
.shop-items .shop-item-pic {
  margin-bottom: 20px;
  width: 100%;
  max-width: 295px;
  position: relative;
}
.shop-items .shop-item-pic:before {
  content: '';
  display: block;
  padding-top: 100%;
}
.shop-items .shop-item-pic img {
  max-width: 100%;
}
.shop-items .shop-item-pic .image_fit_replacer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.shop-items .shop-item-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.shop-items .shop-item-title {
  font-size: 15px;
  font-weight: bold;
  text-transform: uppercase;
  max-width: 100%;
}
@media (max-width: 295+20*2px) {
  .shop-items .shop-item {
    max-width: 100%;
  }
}
